.live-bet-his-main {
    margin-left: 18rem;
  }
  
  .client-live-game {
    background: linear-gradient(to right, #2f5b69 0, #47889e 100%);
    padding: 5px;
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
  }
  .live-casino-date-main {
    display: flex;
    gap: 20px;
    margin-top: 5px;
  }
  .live-casino-input {
    border: 1px solid #007bff;
    width: 15%;
    height: 35px;
  }
  .live-casino-date-main > select {
    width: 15%;
    border: 1px solid #007bff;
  }
  .live-casino-date-main > button {
    width: 60px;
    color: #fff;
    border: none;
    background-color: #5cb85c;
    border-color: #4cae4c;
    font-size: 15px;
    border-radius: 3px;
  }
  .live-casino-table-main {
    margin-top: 5px;
  }
  .live-casino-table-main > table > thead > tr > td {
    background: #b9b9b9;
  }
  @media (max-width: 900px) {
    .live-casino-table-main {
      overflow-y: auto;
    }
    .live-bet-his-main {
      margin-left: 0;
    }
    .live-casino-date-main {
      flex-wrap: wrap;
      justify-content: center;
    }
    .live-casino-input {
      width: 46%;
    }
    .live-casino-date-main > select {
      width: 46%;
    }
  }
  