.login-main {
    background-image: url(../../Assets//Images//bg.webp);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;

    justify-content: center;
    overflow: hidden;
    background-position: 50%;
    background-position-x: 50%;
}

.khelo-logo {
    margin-left: 4.5rem;
}

.input-div {
    margin-left: 4.5rem;
    margin-top: 2rem;
    display: flex;
}

.login-div {
    width: 425px;
    height: 300px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #ffffffd4;
    position: relative;
    height: 425px;
    width: 360px;
    box-shadow: 0px 0px 24px #5c5696;
    border-radius: 25px;
}

.email-input,
.password-input {
    background: transparent;
    height: 40px;
    border-color: #5c5696;
    border-left: none;
    border-top: none;
    border-right: none;
    margin-top: 10px;
    outline: none;
}

.login-btn {
    background: #000;
    font-size: 14px;
    margin-top: 30px;
    padding: 16px 20px;
    border-radius: 10px;
    border: 1px solid #d4d3e8;
    text-transform: uppercase;
    font-weight: 700;
    display: flex;
    align-items: center;
    width: 69%;
    margin-left: 3.5rem;
    color: #fff;
    box-shadow: 0px 2px 2px #5c5696;
    cursor: pointer;
    transition: 0.2s;
}

.login-note {
    font-size: 10px;
    margin-left: 4rem;
    font-weight: 700;
}