.commission-div {
    margin-left: 19rem;
    margin-right: 1rem;
  }
  
  .my-commi {
    width: 100%;
    height: 35px;
  
    background: linear-gradient(to right, #2f5b69 0, #47889e 100%);
    padding: 5px;
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
  }
  
  .comi-date {
    margin-top: 10px;
    display: flex;
    gap: 50px;
  }
  .btn-search {
    width: 100px;
    border: none;
    border-radius: 2px;
    color: #fff;
    background-color: #5cb85c;
    border-color: #4cae4c;
    height: 25px;
  }
  
  @media (max-width: 900px) {
    .commission-div {
      width: 100%;
      margin-left: 0rem;
      margin-right: 0rem;
    }
    .comi-date {
      gap: 9px;
    }
  }
  